import F2 from '@antv/f2'
const WIDTH = document.documentElement.clientWidth * 0.96
const HIGHT = WIDTH * 0.8

/**
 * @Description 
 * @param cxt:canvas
 * @param x:x
 * @param y:y
 * @param width:
 * @param height:
 * @param radius:
 * @param fillColor:
 **/
function fillRoundRect (cxt, x, y, width, height, radius, fillColor) {
  // 
  if (2 * radius > width || 2 * radius > height) {
    return false
  }
  cxt.save()
  cxt.translate(x, y)
  // 
  drawRoundRectPath(cxt, width, height, radius)
  // 
  cxt.fillStyle = fillColor || '#000'
  cxt.fill()
  cxt.restore()
}

function drawRoundRectPath (cxt, width, height, radius) {
  cxt.beginPath(0)
  // 01/2PI
  cxt.arc(width - radius, height - radius, radius, 0, Math.PI / 2)

  // 
  cxt.lineTo(radius, height)

  // 1/2PIPI
  cxt.arc(radius, height - radius, radius, Math.PI / 2, Math.PI)

  // 
  cxt.lineTo(0, radius)

  // PI3/2PI
  cxt.arc(radius, radius, radius, Math.PI, Math.PI * 3 / 2)

  // 
  cxt.lineTo(width - radius, 0)

  // 
  cxt.arc(width - radius, radius, radius, Math.PI * 3 / 2, Math.PI * 2)

  // 
  cxt.lineTo(width, height - radius)
  cxt.closePath()
}

// F2  Todo 
F2.Global.pixelRatio = window.devicePixelRatio
F2.Shape.registerShape('interval', 'borderRadius', {
  draw (cfg, canvas) {
    const points = this.parsePoints(cfg.points)
    const cxt = canvas.getContext('2d')
    const x = points[1].x
    const y = points[1].y
    const width = points[2].x - x
    const height = points[0].y - y
    const radius = width / 5
    const fillColor = cfg.color
    fillRoundRect(cxt, x, y, width, height, radius, fillColor)
  }
})

// 
function createChart (el, data) {
  // Step 1:  Chart 
  const chart = new F2.Chart({
    el: el,
    width: WIDTH,
    height: HIGHT,
    padding: [30, 10, 30, 10]
  })
  // Step 2: 
  chart.source(data, {
    tem: {
      tickCount: 5
    }
  })
  chart.axis('month', {
    label: {
      font: 'PS-Helvetica,  Helvetica, sans-serif, Arial',
      fill: '#79839D'
    },
    line: null,
    grid: null
  })
  chart.axis('tem', {
    label: null,
    grid: null
  })
  // Step 3 genre  sold genre  x sold  y 
  chart
    .interval()
    .position('month*tem')
    .color('isProfit', isProfit => (isProfit ? '#FF4858' : '#6774E4'))
    .shape('borderRadius')
    .size(15)
  // y
  chart.animate({
    type: 'scaley'
  })
  // 
  /* data.forEach((obj, index) => {
    // 
    chart.guide().html([ obj.month, obj.tem ], `<div style='color: #79839D;'><span>${fmoney(obj.tem)}</span></div>`, {
      align: 'bc',
      offset: [ 0, -24 ]
    })
  }) */
  // Step 4: 
  chart.render()
  return chart
}

// 
function fmoney (str, num) {
  let n = num > 0 && num <= 20 ? num : 0
  let s = parseFloat((str + '').replace(/[^\d.-]/g, '')).toFixed(n) + ''
  let l = s.split('.')[0].split('').reverse()
  let r = s.split('.')[1]
  let t = ''
  let i = 0
  let len = l.length
  let res = ''
  for (; i < len; i++) {
    t += l[i] + ((i + 1) % 3 === 0 && (i + 1) !== len ? ',' : '')
  }
  res = t.split('').reverse().join('')
  return r ? (res + '.' + r) : res
}
export default {
  name: 'Chart',
  /* data () {
    return {
      list: [
        { tem: 200, month: '1', isProfit: 0 },
        { tem: 510, month: '2', isProfit: 1 },
        { tem: 59, month: '3', isProfit: 1 },
        { tem: 50, month: '4', isProfit: 0 },
        { tem: 450, month: '5', isProfit: 1 },
        { tem: 140, month: '6', isProfit: 0 },
        { tem: 690, month: '7', isProfit: 1 },
        { tem: 36, month: '8', isProfit: 1 },
        { tem: 146, month: '9', isProfit: 1 },
        { tem: 746, month: '10', isProfit: 0 },
        { tem: 316, month: '11', isProfit: 1 },
        { tem: 349, month: '12', isProfit: 1 }
      ]
    }
  }, */
  props: {
    list: {
      type: Array,
      default () {
        return [
          { tem: 200, month: '1', isProfit: 0 },
          { tem: 510, month: '2', isProfit: 1 },
          { tem: 59, month: '3', isProfit: 1 },
          { tem: 50, month: '4', isProfit: 0 },
          { tem: 450, month: '5', isProfit: 1 },
          { tem: 140, month: '6', isProfit: 0 },
          { tem: 690, month: '7', isProfit: 1 },
          { tem: 36, month: '8', isProfit: 1 },
          { tem: 146, month: '9', isProfit: 1 },
          { tem: 746, month: '10', isProfit: 0 },
          { tem: 316, month: '11', isProfit: 1 },
          { tem: 349, month: '12', isProfit: 1 }
        ]
      }
    }
  },
  mounted () {
    this.chart = createChart(this.$refs.canvas, this.list)
  },
  watch: {
    // todo
    list (val, old) {
      this.chart.guide().clear()
      this.chart.changeData(val)
      val.forEach((obj, index) => {
        // 
        this.chart.guide().html([ obj.month, obj.tem ], `<div style='color: #79839D;'><span>${fmoney(obj.tem)}</span></div>`, {
          align: 'bc',
          offset: [ 0, -24 ]
        })
      })
      this.chart.repaint()
    }
  }
}



